.card__like-button {
    background-color: #ffffff;
    background-image: url("../../../images/heart-icon.svg");
    background-size: contain;
    background-repeat: no-repeat;
    width: 20px;
    height: 18px;
    margin: 0 5px 0 0;
    border-style: none;
    transition: 0.3s linear;
}

.card__like-button:hover {
    cursor: pointer;
    opacity: 0.5; 
}